.App {
  /*text-align: center;*/
  min-height: 100vh;
  background-image: linear-gradient(to top, #a8edea 0%, #fed6e3 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
background: cornflowerblue;
border: none;
padding: 10px 20px;
color: blanchedalmond;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}